.test {
	position: relative;
	height: 100vh;
	background-repeat: no-repeat;
	background-position: bottom right;
	background-size: cover;
	overflow: hidden;

	@include max(md) {
		overflow: hidden;
	}

	&--auto-height {
		min-height: vw(1024);
		height: 100vh;

		@include max(md) {
			min-height: vws(667);
		}
	}

	&.anim-title {
		.test__start-wrapper {
			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}

	&.anim-balls-zoom {
		.test-ball {
			transform: scale(1);
		}
	}

	&.anim-balls-text {
		.test-ball {
			pointer-events: auto;

			p {
				opacity: 1;
				transform: translate(-50%, -50%);
			}
		}
	}

	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}

	&__start {
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 20;
		width: 100%;
		height: 100vh;
		pointer-events: none;
	}

	&__start-wrapper {
		max-width: vw(920);
		position: absolute;
		top: 50%;
		left: 50%;
		opacity: 0;
		transform: translate(-50%, -20%);
		transition: opacity 0.4s ease, transform 0.4s ease;

		@include max(md) {
			max-width: 100%;
			width: 100%;
			padding: 0 vws(45);
		}

		p {
			&:first-child {
				font-size: vw(80);
				line-height: vw(80);

				@include max(md) {
					font-size: vws(40);
					line-height: vws(40);
				}
			}

			&:last-child {
				font-size: vw(22);
				line-height: vw(28);
				font-style: italic;
				margin-top: vw(20);

				@include max(md) {
					font-size: vws(20);
					line-height: vws(26);
					margin-top: vws(10);
				}
			}
		}
	}

	&__arrow {
		width: vw(20);
		height: vw(20);
		position: relative;
		top: 96%;
		left: 50%;
		z-index: 100;
		transform: translateX(-50%);
		animation: arrow-move 3s linear infinite;

		@include max(md) {
			width: vws(20);
			height: vws(20);
		}

		svg {
			width: 100%;
			height: 100%;
		}
	}
}


.section-1 {
	transform: translateY(0);
	transition: transform 1s;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 111;
	width: 100%;

	&.top {
		transform: translateY(-100%);
	}

	.test-ball {
		animation-duration: 10s;
		animation-timing-function: linear;
		animation-iteration-count: infinite;

		@include max(md) {
			display: block !important;
			animation: none;
		}

		&--1 {
			animation-name: move-to-top-right;
		}

		&--2 {
			animation-name: move-to-bottom-left;
			animation-delay: 1s;
		}

		&--3 {
			animation-name: move-to-down-up;
		}

		&--4 {
			animation-name: move-to-top-right;
			animation-delay: 1s;
		}

		&--5 {
			animation-name: move-to-bottom-left;
		}

		&--6 {
			animation-name: move-to-down-up;
			animation-delay: 2s;
		}

		&--7 {
			animation-name: move-to-top-right;
			animation-delay: 1.5s;
		}

		&--8 {
			animation-name: move-to-bottom-left;
			animation-delay: 1s;
		}

		&--9 {
			animation-name: move-to-down-up;
		}

		&--10 {
			animation-name: move-to-top-right;
		}

		&--11 {
			animation-name: move-to-bottom-left;
			animation-delay: 2s;
		}

		&--12 {
			animation-name: move-to-down-up;
			animation-delay: 1.5s;
		}

		&--13 {
			animation-name: move-to-top-right;
		}

		&--14 {
			animation-name: move-to-bottom-left;
			animation-delay: 2s;
		}

		&--15 {
			animation-name: move-to-down-up;
			animation-delay: 2s;
		}

		&--16 {
			animation-name: move-to-top-right;
		}

		&--17 {
			animation-name: move-to-bottom-left;
		}

		&--18 {
			animation-name: move-to-down-up;
			animation-delay: 3s;
		}

		&--19 {
			animation-name: move-to-top-right;
			animation-delay: 1s;
		}

		&--20 {
			animation-name: move-to-bottom-left;
		}
	}
}

.section-2 {
	transform: translateY(100vh);
	transition: transform 1s;
	position: relative;

	&.top {
		transform: translateY(0);
	}
}

body {
	&.blocked {
		overflow: hidden;
	}
}